import { Injectable } from '@angular/core';
import { observable, action, computed } from 'mobx-angular';
import { INote, Note } from '../models/note.model';
import { NotesFilters } from '../constants/notes-filters.enum';
import { SqliteStorageService } from './sqlite-storage.service';

@Injectable({
  providedIn: 'root'
})
export class NotesService {
  table = 'notes';
  @observable notes;
  @observable notesFilter: NotesFilters;

  constructor(
    private sqliteStorage: SqliteStorageService
  ) {}

  @action
  init() {
    this.notes = [];
    this.notesFilter = NotesFilters.Active;
  }

  @action
  addNewNote(note: Partial<INote>) {
    this.notes.push(new Note(note));
  }

  @action
  async getNotes() {
    const notes = await this.sqliteStorage.getAll(this.table);
    this.setNotesList(notes);
  }

  @action
  setNotesList(notes: Array<Note>) {
    this.notes = [...this.notes, ...notes];
  }

  @action
  updateNote(note: Partial<INote>) {
    for (let i = 0, len = this.notes.length; i < len; ++i) {
      if (this.notes[i].Id === note.Id) {
        this.notes[i] = note;
        break;
      }
    }
  }

  @action
  setFilter(filter: NotesFilters) {
    this.notesFilter = filter;
  }

  @action
  archiveNote(note) {
    note.archived = true;
  }

  @computed
  get archivedNotesCount() {
    return this.notes.filter(note => !!note.archived).length;
  }

  @computed
  get filteredNotes() {
    switch (this.notesFilter) {
      case NotesFilters.Active:
        return this.notes.filter(note => !note.archived);
      case NotesFilters.Archived:
        return this.notes.filter(note => !!note.archived);
    }
  }
}
